td {
  margin: 0;
  padding: 0;
}
html,
body {
  width: 100%;
  height: 100%;
  color: #121732;
  font: 12px/150% Tahoma, "\5FAE\8F6F\96C5\9ED1", "\5B8B\4F53", Arial, Helvetica, sans-serif;
  color: #333;
}
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
.clear {
  clear: both;
}
fieldset,
img {
  border: 0;
}
a {
  text-decoration: none;
  color: #666;
}
a:hover {
  color: #333;
  text-decoration: none;
}
address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var,
b,
i {
  font-style: normal;
  font-weight: normal;
}
ol li,
ul li {
  list-style: none;
}
caption {
  text-align: left;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
}
q:before,
q:after {
  content: '';
}
abbr,
acronym {
  border: 0;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
