/**/
.tl {
  text-align: left;
}
.tc {
  text-align: center;
}
.tr {
  text-align: right;
}
.bc {
  margin-left: auto;
  margin-right: auto;
}
.fl {
  float: left;
  display: inline;
}
.fr {
  float: right;
  display: inline;
}
.cb {
  clear: both;
}
.cl {
  clear: left;
}
.cr {
  clear: right;
}
.clearfix:after {
  clear: both;
  content: "";
  display: block;
  height: 0;
  visibility: hidden;
  font-size: 0px;
}
.clearfix {
  zoom: 1;
}
.vm {
  vertical-align: middle;
}
.pr {
  position: relative;
}
.pa {
  position: absolute;
}
.zoom {
  zoom: 1;
}
.hidden {
  visibility: hidden;
}
.block {
  display: block;
}
.inline {
  display: inline;
}
.in_b {
  display: inline-block;
}
.in_b {
  *display: inline;
}
.none {
  display: none;
}
.oh {
  overflow: hidden;
}
.ov {
  overflow: visible;
}
.bold {
  font-weight: bold;
}
.red {
  color: #D32D26;
}
.blue {
  color: #1B226E;
}
.grey {
  color: #949BA5;
}
.white {
  color: #fff;
}
.yellow {
  color: #EA9D40;
}
.whitebg {
  background-color: #fff;
}
.mint-cell-wrapper,
.mint-cell:last-child {
  background-image: none;
}
.mint-cell {
  padding-left: 14px;
  border-radius: 28px;
  border: 1px solid #f0f0f0;
  margin-bottom: 16px;
}
.ellipsis {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  word-break: break-all;
}
.ellipsis2 {
  text-overflow: -o-ellipsis-lastline;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  -webkit-box-orient: vertical;
}
